.logo {
  position: absolute;
  font-family: $font-2;
  font-size: 1rem;
  user-select: none;
  color: $color-2;

  &:after {
    content: "";
    height: 24px;
    width: 24px;
    border: 1px $color-2 solid;
    border-radius: 50%;
    position: absolute;
    top: 1px;
    left: -12px;
    z-index: -1;
  }
}